@charset "UTF-8";
.container {
  max-width: 1180px;
  padding: 0 18px;
  position: relative;
  width: 100%;
  margin-right: auto;
  margin-left: auto;
  display: flex;
  flex-direction: column;
}

.content {
  display: flex;
  align-items: center;
}
.content--justify-between {
  justify-content: space-between;
}
.content--column {
  flex-direction: column;
}
.content--align-start {
  align-items: flex-start;
}

.flex-wrap {
  flex-wrap: wrap;
}

.margin-auto {
  margin: auto;
}

.title {
  display: flex;
  width: 100%;
  color: #151515;
  font-family: "Gotham Pro Bold";
  font-size: 16px;
  line-height: 22px;
  margin: 0;
}

.title--little {
  font-size: 14px;
}

.text {
  font-size: 12px;
  line-height: 18px;
  color: #151515;
}

.favorites__title {
  margin-top: 20px;
  margin-bottom: 10px;
}

.mt-20 {
  margin-top: 20px !important;
}

.title-full-page {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  height: calc(100vh - 410px);
}

.hidden {
  display: none !important;
}

.active {
  display: flex;
}

.gray-bg {
  background-color: #f1f2f4;
  margin-left: -18px;
  margin-right: -18px;
  padding: 0 18px 20px;
  margin-top: 20px;
}

.title--margin {
  margin-top: 27px;
  margin-bottom: 20px;
}

.m-b-10 {
  margin-bottom: 10px;
}

.m-b-30 {
  margin-bottom: 30px;
}

.content--stretch {
  align-items: stretch;
}

.content--justify-center {
  justify-content: center;
}

.desc--no-margin {
  margin: 0;
}

.img {
  width: 100%;
  height: auto;
  margin-top: 25px;
}

.gap-16 {
  gap: 16px;
}

.mb-30 {
  margin-bottom: 30px;
}

.text-info {
  color: #8E8E8E;
  font-size: 12px;
  margin-top: 20px;
}

.no-bg {
  background: none;
}

.quote,
.quote__img-wrapper {
  display: flex;
  align-items: center;
}

.quote {
  justify-content: space-between;
  gap: 20px;
}

.quote__img-wrapper {
  overflow: hidden;
  border-radius: 15px;
  justify-content: center;
  min-width: 140px;
  width: 140px;
  height: 150px;
}

.about__control-button, .about__more {
  display: flex;
  align-items: center;
  outline: 0;
}

.quote__img {
  min-width: 100%;
  min-height: 100%;
  height: auto;
}

.quote__author {
  color: #3297d7;
  font-size: 15px;
  font-family: "Gotham Pro Bold";
  margin-bottom: 0;
}

.quote__author-position {
  font-size: 13px;
  margin-top: 10px;
  margin-bottom: 20px;
}

.quote__text {
  color: #8e8e8e;
  font-size: 13px;
  line-height: 16px;
  font-family: "Gotham Pro Italic";
}

.about__text-content {
  color: #151515;
  font-size: 13px;
  line-height: 22px;
}

.about__more {
  background-color: transparent;
  border: none;
  color: #3297d7;
  font-size: 14px;
  padding: 0;
  justify-content: flex-start;
  position: relative;
  border-bottom: 1px dashed #3297d7;
  margin: auto;
}
.about__more::after {
  content: "";
  background-image: url("data:image/svg+xml,%3Csvg width='8' height='9' viewBox='0 0 8 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4 0V8M4 8L1 5.66667M4 8L7 5.66667' stroke='%233297D7' stroke-width='1.5'/%3E%3C/svg%3E");
  background-size: contain;
  width: 10px;
  height: 10px;
  position: absolute;
  right: -14px;
  top: 4px;
}

.m-t-40 {
  margin-top: 40px !important;
}

.about__carousel-control {
  display: flex;
  flex-wrap: nowrap;
  gap: 12px;
  margin-bottom: 34px;
  margin-top: 30px;
  overflow: auto;
}
.about__carousel-control::-webkit-scrollbar {
  width: 0;
}

.about__control-button {
  background: #f1f2f4;
  border-radius: 8px;
  padding: 6px 20px;
  height: 30px;
  justify-content: center;
  border: none;
  font-weight: 400;
  font-size: 11px;
  line-height: 16px;
}
.about__control-button.active {
  background-color: #cbe7ff;
}

.about__carousel-date,
.product__label {
  font-family: "Gotham Pro Bold";
  font-weight: 700;
}

.about__carousel-date,
.about__carousel-desc {
  font-size: 13px;
  line-height: 22px;
}

.about__carousel-img {
  width: 100%;
}

.about__carousel-date {
  font-style: normal;
}

.about__carousel-item {
  display: none;
}

.about__carousel-item.active {
  display: block;
}

.about__carousel {
  display: none;
}
.about__carousel.active {
  display: block;
}

.account-info__label {
  font-size: 11px;
  line-height: 16px;
  color: #959595;
  margin: 24px 0 0;
}

.account-info__value {
  font-size: 13px;
  line-height: 16px;
  color: #151515;
  margin: 4px 0 0;
}

.account-edit-link {
  font-size: 13px;
  line-height: 16px;
  text-decoration: underline;
  color: #3298d7;
  margin-top: 30px;
  display: flex;
  background: transparent;
  border: none;
  padding: 0;
}

.account-info-wrapper {
  margin-bottom: 30px;
}
.account-info-wrapper .select-wrapper {
  margin-top: 6px;
}
.account-info-wrapper .selected {
  height: 38px;
  padding: 10px 40px 8px 10px;
  border: 2px solid #3298d7;
  font-size: 13px;
  line-height: 16px;
}
.account-info-wrapper .select-item {
  padding: 10px 40px 8px 10px;
  background: #3298d7;
  font-size: 13px;
  line-height: 16px;
  color: #fff;
}

.account-info--confirmed {
  position: relative;
}
.account-info--confirmed::after {
  content: "Подтвержден";
  font-size: 11px;
  line-height: 16px;
  color: #3298d7;
  position: relative;
  left: 20px;
  top: 2px;
}
.account-info--confirmed::before {
  background-image: url("data:image/svg+xml,%3Csvg width='15' height='14' viewBox='0 0 15 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Ccircle cx='7' cy='7' r='6.25' stroke='%233297D7' stroke-width='1.5'/%3E%3Cpath d='M6.65517 6.2L5.51724 5L4 6.2L8.55172 11L15 4.6L11.9655 1L6.65517 6.2Z' fill='white'/%3E%3Cpath d='M4 6.81818L6.33333 9L11 5' stroke='%233297D7' stroke-width='1.5'/%3E%3C/svg%3E");
  content: "";
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 15px;
  width: 15px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.account-management__button {
  margin-top: 0 !important;
}

.account-form .text-field {
  border: 2px solid #c4c4c4;
  margin-top: 6px;
  padding: 6px 10px;
}
.account-form .account-info__label {
  margin-top: 4px;
}

.account-data-links {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 0;
  margin-bottom: 30px;
}
.account-data-links .account-edit-link {
  margin-top: 0;
  margin-bottom: 14px;
}

.action-bar {
  position: fixed;
  bottom: 60px;
  z-index: 2;
  display: flex;
  align-items: center;
  width: 100%;
}

.action-button {
  width: 50%;
  height: 50px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  border: none;
  color: #fff;
  background-color: #f54082;
  font-family: "Gotham Pro Bold";
  font-weight: 700;
  font-size: 12px;
  line-height: 22px;
}

.action-button--in-cart {
  background-color: #0060d1;
}
.action-button--in-cart span {
  font-family: "Gotham Pro Regular";
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
}

#one-click {
  color: #3297d7;
  background-color: #f1f2f4;
}

