.text-field {
  width: 100%;
  border: 1px solid #c4c4c4;
  border-radius: 3px;
  color: #151515;
  font: inherit;
  outline: 0;
  padding: 8px 18px;
  font-size: 12px;
  line-height: 22px;
}
.text-field:focus + .text-field-dropdown {
  display: block;
}

.text-field-wrapper {
  margin-top: 10px;
