  flex-direction: column;
  position: relative;
}

.form-label {
  font-size: 11px;
  line-height: 16px;
  margin-bottom: 5px;
}

.checkbox, .radio {
  position: absolute;
  left: -9999px;
  opacity: 0;
  z-index: -100;
}

.checkbox-label, .radio-label {
  font-weight: 400;
  font-size: 11px;
  line-height: 16px;
  position: relative;
  padding-left: 28px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  white-space: nowrap;
  height: 20px;
}

.checkbox-label::after, .checkbox-label::before, .radio-label::after, .radio-label::before {
  content: "";
  position: absolute;
}

.checkbox-label::before, .radio-label::before {
  height: 20px;
  width: 20px;
  border: 1px solid #3298d7;
  border-radius: 3px;
  left: 0;
}

.checkbox-label::after, .radio-label::after {
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='9' viewBox='0 0 12 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 3.5L5.28571 7L11 1' stroke='white' stroke-width='1.5'/%3E%3C/svg%3E");
  height: 10px;
  width: 22px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  left: 0;
}

.checkbox:checked + .checkbox-label::before {
  background-color: #3298d7;
}

.checkbox-label--main-color {
  margin-top: 20px;
  color: #3298d7;
}
.checkbox-label--main-color a {
  color: #3298d7 !important;
}

.radio-label {
  padding: 20px 18px;
  border-bottom: 1px solid #e5e5e5;
}
.radio-label::before {
  border-radius: 50%;
  right: 18px;
  left: auto;
  border-width: 2px;
}
.radio-label::after {
  height: 10px;
  width: 10px;
  background: #fff;
  right: 25px;
  left: auto;
  border-radius: 50%;
}
.radio-label:nth-child(2) {
  border-top: 1px solid #e5e5e5;
}

.radio:checked + .radio-label {
  color: #3298d7;
}
.radio:checked + .radio-label::after {
  background: #3298d7;
}

