  animation-duration: 1s;
  animation-delay: 2000ms;
  animation-name: star;
  animation-iteration-count: infinite;
  animation-direction: alternate;
}

.star--1 {
  display: block;
  opacity: 0.7;
  left: 31px;
  top: 27px;
  animation-delay: 4s;
}

.star--2 {
  display: block;
  opacity: 1;
  width: 4px;
  height: 4px;
  left: 54px;
  top: 56px;
}

.star--3 {
  display: block;
  opacity: 0.7;
  left: 29px;
  top: 94px;
  animation-delay: 2.3s;
}

.star--4 {
