.share__title {
  font-size: 11px;
  line-height: 22px;
  font-family: "Gotham Pro Bold";
  margin-bottom: 6px;
}

.popup-wrapper {
  z-index: 1001;
  background: rgba(0, 0, 0, 0.5);
  position: fixed;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  align-items: center;
  justify-content: center;
  display: none;
}
.popup-wrapper.active {
  display: flex;
}
.popup-wrapper--note {
  height: auto;
  top: 70px;
  background: transparent;
}
.popup-wrapper--note .popup, .popup-wrapper--note .popup__header {
  background: #97FCA7;
  color: #186223;
}
.popup-wrapper--note .popup__close::before, .popup-wrapper--note .popup__close::after {
  background-color: #186223;
}
.popup-wrapper--note .popup__header {
  padding-bottom: 0;
  padding-top: 0;
  margin-top: -16px;
}
.popup-wrapper--note .popup__main {
  padding-top: 10px;
}
.popup-wrapper--warning .popup, .popup-wrapper--warning .popup__header {
  background: #FFFFB2;
  color: #7a7a01;
}
.popup-wrapper--warning .popup__close::before, .popup-wrapper--warning .popup__close::after {
  background: #7a7a01;
}
.popup-wrapper--error .popup, .popup-wrapper--error .popup__header {
  background: #ff8c86;
  color: #8F2A26;
}
.popup-wrapper--error .popup__close::before, .popup-wrapper--error .popup__close::after {
  background: #8F2A26;
}
.popup-wrapper--message .popup__header {
  background: #3298d7;
}
.popup-wrapper--header-error .popup__header {
  background: #E25049;
}
.popup-wrapper--list .popup__main {
  max-height: calc(100vh - 400px);
  overflow: auto;
  gap: 10px;
}

.popup {
  background: #fff;
  width: calc(100vw - 36px);
  overflow: hidden;
  border-radius: 10px;
}
.popup .select {
  max-height: 50px;
  overflow: auto;
}

.popup__close {
  position: relative;
  height: 20px;
  width: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 16px;
  margin-right: 16px;
  margin-left: auto;
}
.popup__close::before, .popup__close::after {
  content: "";
  position: absolute;
  height: 16px;
  width: 2px;
  background: #3298d7;
}
.popup__close::before {
  transform: rotate(-45deg);
}
.popup__close::after {
  transform: rotate(45deg);
}

.popup__header {
  padding: 16px;
  background-color: #FFAD38;
  color: #fff;
  font-family: "Gotham Pro Bold";
  font-size: 14px;
  letter-spacing: 0.5px;
}

.popup__main {
  display: flex;
  flex-direction: column;
  padding: 16px;
  font-size: 14px;
}

.popup__footer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 10px 18px 10px;
  gap: 6px;
}

.popup__button {
  border-radius: 13px;
  padding: 2px 20px;
}

.popup-list {
  position: absolute;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.popup-list .popup-wrapper {
  position: relative;
}

.load {
  height: 70px;
  width: 70px;
  margin: auto;
}