
html 
  line-height: 1.15 
  -webkit-text-size-adjust: 100% 
body 
  margin: 0 
  width: 100vw 
  min-height: 100vh 
details,main 
  display: block 
h1 
  font-size: 2em 
hr 
  box-sizing: content-box 
  height: 0 
  overflow: visible 
code,kbd,pre,samp 
  font-family: monospace,monospace 
  font-size: 1em 
a 
  background-color: transparent 
  text-decoration: none 
abbr[title] 
  border-bottom: none 
  text-decoration: underline 
  -webkit-text-decoration: underline dotted 
  text-decoration: underline dotted 
b,strong 
  font-weight: bolder 
small 
  font-size: 80% 
sub,sup 
  font-size: 75% 
  line-height: 0 
  position: relative 
  vertical-align: baseline 
sub 
  bottom: -.25em 
sup 
  top: -.5em 
img 
  border-style: none 
button,input,optgroup,select,textarea 
  font-family: inherit 
  font-size: 100% 
  line-height: 1.15 
  margin: 0 
button,input 
  overflow: visible 
button,select 
  text-transform: none 
[type=button],[type=reset],[type=submit],button 
  -webkit-appearance: button 
[type=button]::-moz-focus-inner,[type=reset]::-moz-focus-inner,[type=submit]::-moz-focus-inner,button::-moz-focus-inner 
  border-style: none 
  padding: 0 
fieldset 
  padding: .35em .75em .625em 
legend 
  box-sizing: border-box 
  color: inherit 
  display: table 
  max-width: 100% 
  padding: 0 
  white-space: normal 
progress 
  vertical-align: baseline 
textarea 
  overflow: auto 
[type=checkbox],[type=radio] 
  box-sizing: border-box 
  padding: 0 
[type=number]::-webkit-inner-spin-button,[type=number]::-webkit-outer-spin-button 
  height: auto 
[type=search] 
  -webkit-appearance: textfield 
  outline-offset: -2px 
[type=search]::-webkit-search-decoration 
  -webkit-appearance: none 
::-webkit-file-upload-button 
  -webkit-appearance: button 
  font: inherit 
summary 
  display: list-item 
[hidden],template 
  display: none 
@font-face 
  font-family: "Gotham Pro Regular" 
  src: url(../fonts/GothamPro.woff) format("woff") 
@font-face 
  font-family: "Gotham Pro Italic" 
  src: url(../fonts/GothamPro-Italic.woff) format("woff") 
@font-face 
  font-family: "Gotham Pro Bold" 
  src: url(../fonts/GothamPro-Bold.woff) format("woff") 
@font-face 
  font-family: "Gotham Pro Bold Italic" 
  src: url(../fonts/GothamPro-BoldItalic.woff) format("woff") 
@font-face 
  font-family: "Gotham Pro Light" 
  src: url(../fonts/GothamPro-Light.woff) format("woff") 
h1,h2,h3,h4,h5,h6,ul 
  margin: 0 
  padding: 0 
* 
  box-sizing: border-box 
body,html 
  position: relative 
  height: 100% 
  font-size: 16px 
  font-family: "Gotham Pro Regular" 
